
* {
    box-sizing: border-box;
}

html, body {
    height: 100%;
    margin: 0;
    padding: 0;
    background: #F6CFC8;
    color: #4E4F4D;
    font-family: 'Josefin Slab', serif;
}

.compass-wrapper {
    display: flex;
    justify-content:center;
    align-items: center;
    flex-direction: column;
    height:100%;
}

.compass-micro {
    position: absolute;
    bottom: 2rem;
}

.compass-container {
    width:100vw;
    height: 100vw;
    padding: 5vw;
}

.compass {
    width:100%;
    height:100%;
    background: #f39292;
    border: 10px solid #4E4F4D;
    border-radius: 100%;
}

.compass-needle {
    width:2vw;
    height:80%;
    position: relative;
    left: calc(50% - 5px);
    top: calc(10% - 5px);
    background: #4E4F4D;
}

.compass-needle:after {
    content: '';
    display: block;
    width:8vw;
    height:10vw;
    right: -3vw;
    position: absolute;
    bottom:-4.5vw;
    background: #4E4F4D;
}

.compass-needle__arrow {
    display: block;
    right: -4vw;
    position: absolute;
    top:-4.5vw;
    width: 0;
    height: 0;
    border-left: 5vw solid transparent;
    border-right: 5vw solid transparent;
    border-bottom: 9vw solid #4E4F4D;
}

.map {
    height: 50vw;
}

.c-loading--screen {
    font-family: 'Niconne', cursive;
    font-size: 3rem;
    position: fixed;
    top:0;
    left:0;
    width:100vw;
    height:100vh;
    background: #F6CFC8;
    color: #4E4F4D;

    padding:1rem;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.c-micro {
    font-size:1rem;
}

.c-loading__text {
    position: absolute;
    bottom: 5px;
}


form {
    width:100%;
    padding:1rem;
}


label {
    width:100%;
    display: block;
    margin-bottom:.3rem;
    font-size:1.8rem;
}

input, #submit {
    width:100%;
    font-size:1.8rem;
    margin-bottom: 1rem;
    padding: .5rem;
    background: transparent;

    border: 1px solid #4E4F4D;
    border-radius: 1rem;
}

#submit {
    background: #f39292;
    border: 1px solid #f39292;
    color: #4E4F4D;
    align-items: center;
    display: flex;
    justify-content: center;
}

#submit svg {
    width:2rem;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    font-family: 'Josefin Slab', serif;
    color: #4E4F4D;
    opacity: .5;
}
::-moz-placeholder { /* Firefox 19+ */
    font-family: 'Josefin Slab', serif;
    color: #4E4F4D;
    opacity: .5;
}
:-ms-input-placeholder { /* IE 10+ */
    font-family: 'Josefin Slab', serif;
    color: #4E4F4D;
    opacity: .5;
}
:-moz-placeholder { /* Firefox 18- */
    font-family: 'Josefin Slab', serif;
    color: #4E4F4D;
    opacity: .5;
}



